<template>
  <div id="app">
    <div class="toUp-main">
      <van-nav-bar
        title="转账"
        left-arrow
        @click-left="onBack"
      />
      <div class="toUp-header">
        <p>当前积分</p>
        <h3>10000.0</h3>
      </div>
      <div class="toUp-con">
        <div class="toUp-con-zz">
          <van-cell-group>
            <van-field
              v-model="username"
              required
              label="收款人姓名"
              placeholder="请输入收款人姓名"
            />
            <van-field
              label="收款人电话"
              placeholder="请输入收款人电话"
              required
            />
            <van-field
              label="转账金额"
              placeholder="请输入转账金额"
              required
            />
          </van-cell-group>
        </div>
        <van-button class="toUp-con-btn" type="default">确认转账</van-button>
        <div class="toUp-sm">
          转账说明：只有积分可以转账，转到对方将变成“余额”，转账余额只能报单，不可提现。转账完成后不可回转。
        </div>
      </div>
    </div>

  </div>
</template>

<script>

export default {
  name: 'transfer',
  data () {
    return {}
  },
  methods: {
    onBack: function () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="less" scoped>
  @import url('../assets/css/home.less');
</style>



