<template>
  <!-- 首页组件 -->
  <div id="app">
    <van-nav-bar
      title="威尼斯酒店"
    />
    <Swiper class="swiper" :tabs="images"></Swiper>
    <div class="card-main">
      <div class="card-item">
        <ul>
          <li>
            <van-col class="card-title" span="12">微会员卡</van-col>
            <van-col class="card-status" span="12">已激活</van-col>
          </li>
          <li class="card-h"><p>6229884436094837453</p></li>
          <li class="card-rank">
            <sapn>威尼斯酒店VIP</sapn>
          </li>
        </ul>
      </div>
      <div class="account-information">
        <van-col class="account-information-item" span="7">
          <span>余额</span>
          <span>￥0.0</span>
        </van-col>
        <van-col class="account-information-item" span="7">
          <span>积分</span>
          <span>0.0</span>
        </van-col>
        <van-col class="account-information-item" span="7">
          <span>复投卷</span>
          <span>0.0</span>
        </van-col>
      </div>
    </div>
    <van-tabbar v-model="tabarActive">
      <van-tabbar-item icon="wap-home" @click="redirects('/')">主页</van-tabbar-item>
      <van-tabbar-item icon="wap-nav" @click="redirects('/community')">功能</van-tabbar-item>
      <van-tabbar-item icon="contact" v-infos="my_info" @click="redirects('/me')">我的</van-tabbar-item>
    </van-tabbar>
  </div>
  <!--<Tab :tabarActive="0"></Tab>-->
</template>

<script>
import Swiper from './swiper'
import Tab from './tab'

export default {
  name: 'home',
  components: { Tab, Swiper },
  data () {
    return {
      tabarActive: 0,
      images: [
        'https://a2.vimage1.com/upload/flow/2018/06/12/50/15287856410421.jpg',
        'https://a2.vimage1.com/upload/flow/2018/06/12/50/15287856410421.jpg',
        'https://a2.vimage1.com/upload/flow/2018/06/12/50/15287856410421.jpg'
      ]
      // wxAppID: ''
    }
  },
  methods: {
    redirects (url) {
      this.$router.push(url)
    },
    getWxCode () {
      const uri = ''
      const redirectUri = encodeURIComponent(uri)
      const url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + this.wxAppID + '&redirect_uri=' + redirectUri + '&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
      // console.log('url:', url)
      location.replace(url)
    }
  },
  created () {
    // this.getWxCode()
  }
}
</script>

<style lang="less" scoped>
  @import url('../assets/css/home.less');

  body {
    color: #333333;
    font-family: BlinkMacSystemFont, "Helvetica Neue", Arial, "Microsoft Yahei", "WenQuanYi Micro Hei", "Pingfang SC", sans-serif;
  }
</style>



