<template>
  <div id="app">
    <div class="toUp-main">
      <van-nav-bar
        title="复投"
        left-arrow
        @click-left="onBack"
      />
      <div class="toUp-header">
        <p>当前复投卷</p>
        <h3>10000</h3>
      </div>
      <div class="toUp-con">
        <p>复投金额</p>
        <van-field
          left-icon="after-sale"
        />
        <van-button class="toUp-con-btn" type="default">确认复投</van-button>
        <div class="toUp-sm">
          复投说明：复投金额只能是600的倍数，复投后不能取消。
        </div>
      </div>
    </div>

  </div>
</template>

<script>

export default {
  name: 'withdrawal',
  data () {
    return {}
  },
  methods: {
    onBack: function () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="less" scoped>
  @import url('../assets/css/home.less');
</style>



