<template>
  <div id="app">
    <div>
      <div class="user-header">
        <van-col span="10" class="user-img">
          <img src="https://pic.qqtn.com/up/2018-8/15332606302773710.jpg" alt="头像">
        </van-col>
        <van-col span="14" class="user-message">
          <p>姓名: 张三</p>
          <p>奖金统计: 100000.0</p>
          <p>激活日期: 2018-09-29</p>
        </van-col>
      </div>
      <van-row class="user-links user-links-list">
        <van-col span="8" class="list-item">
          <span>可提成数</span>
          <span>10层</span>
        </van-col>
        <van-col span="8" class="list-item">
          <span>直推人数</span>
          <span>10人</span>
        </van-col>
        <van-col span="8" class="list-item">
          <span>积分余额</span>
          <span>0.0</span>
        </van-col>
      </van-row>
    </div>
    <van-cell-group class="user-group">
      <van-cell icon="records" title="修改二级密码" @click="redirects('/changePassword')"/>
    </van-cell-group>
    <van-tabbar v-model="tabarActive">
      <van-tabbar-item icon="wap-home" @click="redirects('/')">主页</van-tabbar-item>
      <van-tabbar-item icon="wap-nav" @click="redirects('/community')">功能</van-tabbar-item>
      <van-tabbar-item icon="contact" v-infos="my_info" @click="redirects('/me')">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  name: 'me',
  data () {
    return {
      tabarActive: 2
    }
  },
  methods: {
    redirects (url) {
      this.$router.push(url)
    }
  },
  created () {
    // this.getWxCode()
  }
}
</script>

<style lang="less">
  .user {
    &-poster {
      width: 7.5rem;
      height: 4rem;
      display: block;
    }
    &-group {
      margin-bottom: .3rem;
    }
    &-links {
      padding: 15px 0;
      font-size: 12px;
      text-align: center;
      background-color: #fff;
      .van-icon {
        display: block;
        font-size: 24px;
      }
    }
  }

  .user-header {
    box-sizing: border-box;
    width: 100%;
    height: 180px;
    padding: 5%;
    background: url("../../static/images/jdXqOp2qmP.jpg") no-repeat;
    background-size: cover;
    .user-img {
      margin-top: 6%;
      img {
        width: 105px;
        height: 105px;
        border-radius: 50%;
      }
    }
    .user-message {
      box-sizing: border-box;
      padding-left: 2%;
      text-align: left;
      margin-top: 8.5%;
      p {
        margin: 0;
        line-height: 30px;
        color: #ffffff;
        font-size: 1rem;
      }
    }
  }

  .user-links-list {
    .list-item {
      border-right: 1px solid #eee;
      height: 60px;
      /*border-right: none;*/
      span {
        display: inline-block;
        line-height: 30px;
        width: 100%;
        /*color: #fff;*/
      }
    }
    .list-item:nth-last-of-type(1) {
      border-right: none;
    }
  }
</style>



