<template>
  <div id="app">
    <div class="toUp-main">
      <van-nav-bar
        title="复投卷"
        left-arrow
        @click-left="onBack"
      />
      <div class="toUp-header">
        <p>当前积分</p>
        <h3>10000.0</h3>
      </div>
      <div class="toUp-con">
        <p>提现金额</p>
        <van-field
          left-icon="after-sale"
        />
        <van-button class="toUp-con-btn" type="default">确认提现</van-button>
        <div class="toUp-sm">
          提现说明：提现后，公司将打款给您，请与公司取得联系。提现将扣除5%手续费，到手即提现金额的95%。
        </div>
      </div>
    </div>

  </div>
</template>

<script>

export default {
  name: 'withdrawal',
  data () {
    return {}
  },
  methods: {
    onBack: function () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="less" scoped>
  @import url('../assets/css/home.less');
</style>



