<template>

  <div id="changePassword">
    <van-nav-bar
      title="修改二级密码"
      left-arrow
      @click-left="onBack"
    />
    <van-cell-group>
      <van-field
        v-model="password"
        type="password"
        label="原密码"
        placeholder="请输入密码"
        required
      />
      <van-field
        v-model="password"
        type="password"
        label="新密码"
        placeholder="请输入密码"
        required
      />
      <van-field
        v-model="password"
        type="password"
        label="确认密码"
        placeholder="请输入密码"
        required
      />
    </van-cell-group>
  </div>
</template>

<script>

export default {
  name: 'changePassword',
  data () {
    return {}
  },
  methods: {
    onBack: function () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="less" scoped>
  @import url('../assets/css/search.less');
  .van-cell-group{
    margin-top: 5% !important;
  }
  .van-cell{
    /*border: 1px solid #eee;*/
  }
</style>



