<template>
  <div id="app">
    <div class="toUp-main">
      <van-nav-bar
        title="充值"
        left-arrow
        @click-left="onBack"
      />
      <div class="toUp-header">
        <p>当前余额</p>
        <h3>¥10000.0</h3>
      </div>
      <div class="toUp-con">
        <p>充值金额</p>
        <van-field
          left-icon="after-sale"
        />
        <van-button class="toUp-con-btn" type="default">确认充值</van-button>
        <div class="toUp-sm">
          充值说明：充值后，请打款给公司财务，财务收到款后,会给予该笔申请进行审核。充值后的余额只能报单。
        </div>
      </div>
    </div>

  </div>
</template>

<script>

export default {
  name: 'topUpCenter',
  data () {
    return {}
  },
  methods: {
    onBack: function () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="less" scoped>
  @import url('../assets/css/home.less');
</style>



